import Axios from 'axios';

export const login = user => (
  Axios({
    method: 'POST',
    url: '/api/session',
    data: { user }
  })
);

export const fetchCurrentUserData = () => (
  Axios({
    method: 'GET',
    url: '/api/users/data',
  })
);

export const signup = user => (
  Axios({
    method: 'POST',
    url: '/api/users',
    data: { user }
  })
);

export const logout = () => (
  Axios({
    method: 'DELETE',
    url: '/api/session'
  })
);

export const editUser = (formData) => (
  Axios({
    url: `/api/users/${formData.get('user[id]')}`,
    method: 'PATCH',
    data: formData,
    contentType: false,
    processData: false
  })
);